import global from '../global'
cc.Class({
    extends: cc.Component,

    properties: {
        timeCutDownLabel: {
            default: null,
            type: cc.Label
        },
        resultLabel: {
            default: null,
            type: cc.Label
        },
        labelTip:cc.Label,
        labelWord:cc.Label,
        spAnswerList:[cc.Sprite],
        spReadImage:cc.Sprite,
        auCardIn:cc.AudioClip,
        auSuccess:cc.AudioClip,
        nodeChoose:cc.Node,
        nodeRead:cc.Node
    },

    getQuestion(){
        let list=global.account.playerData.questions;
        if(list.length<=0){
            console.error("question list is empty");
            return;
        }
        if(global.account.playerData.questionIndex>=list.length){
            global.account.playerData.questionIndex=0;
        }
        return list[global.account.playerData.questionIndex];
    },

    changeImageTexture(imageSprite,url){
        let texture=cc.textureCache.getTextureForKey(url);
        if(texture){
            imageSprite.spriteFrame.clearTexture();
            imageSprite.spriteFrame.setTexture(texture);
        }
    },

    showQuestion(){
        cc.audioEngine.play(this.auCardIn);
        let question=this.getQuestion();
        console.log("question:",question);
        this.question=question;
        if(question.type===1){  //
            this.nodeRead.active=true;
            this.nodeChoose.active=false;
            this.labelTip.string="";
            this.labelWord.string=question.word;
            this.changeImageTexture(this.spReadImage,question.image_url);
            this.animation.play("an_show_question_read");
        }else{
            this.nodeRead.active=false;
            this.nodeChoose.active=true;
            this.labelTip.string="";
            this.labelWord.string=question.word;
            for(let i=0;i<this.question.image_urls.length;i++){
                let url=this.question.image_urls[i];
                let sprite=this.spAnswerList[i];
                this.changeImageTexture(sprite,url);
            }
            this.animation.play("an_show_question_choose");
        }
        global.account.playerData.questionIndex++;
    },

    onLoad(){
        //["http://localhost:8080/1.jpg","http://localhost:8080/2.jpg","http://localhost:8080/3.jpg"]
        cc.loader.load(["http://47.52.247.115/1.jpg","http://47.52.247.115/2.png","http://47.52.247.115/3.jpg"],function (err, items) {
            if(err){
                console.error(err);
                return;
            }
            for (let i in items.map){
                if(!cc.textureCache.getTextureForKey(items.map[i].content)){
                    cc.textureCache.addImage(items.map[i].content);
                }
            }
        });
        let self=this;
        // global.account.playerData.questionIndex=0;
        this.animation=this.node.getComponent(cc.Animation);
        this.animation.on("finished",this.onUiAnimationFinished.bind(this));
        global.account.gameCtl.onTipsBegan((data)=> {
            console.log('' + JSON.stringify(data));
            self.node.active = true;
            self.timeCutDown = 5;
            self.showQuestion();
        });

        // global.account.gameCtl.onTipsEnd(()=>{
        //     this.node.active = false;
        // });
    },

    start () {
        this.node.active = false;
    },

    hideQuestion(){
        if(this.question.type===1){
            this.animation.play("an_hidden_question_read");
        }else{
            this.animation.play("an_hidden_question_choose");
        }
        global.account.playerData.power++;
        global.account.gameCtl.tipsEnd({
            result: 'success'
        });
        if(global.account.playerData.power>=4){
            global.account.gameCtl.quick();
            global.account.playerData.power=0;
        }
        cc.audioEngine.play(this.auSuccess);
    },

    onUiAnimationFinished(event){
        var state = event.detail;    // state instanceof cc.AnimationState
        var type = event.type;       // type === 'play';
        if(state.name.indexOf("_hidden_")>=0&&type==="finished"){
            this.node.active=false;
        }
    },

    update(dt){
        if (global.account.gameCtl.isRunning()){
            if (this.timeCutDown > 0){
                this.timeCutDown -= dt;
                if (this.timeCutDown <= 0){
                    this.timeCutDown = 0;
                    this.resultLabel.string = '!';
                    // setTimeout(()=>{
                    //     this.resultLabel.string = '';
                    // }, 1000);
                    this.hideQuestion();
                }
                this.timeCutDownLabel.string = Math.floor(this.timeCutDown) + '';
            }
        }
    },

    onChooseAnswerClicked(event,customData){
        if (parseInt(customData)!==this.question.answer_index){
            global.account.gameCtl.tipsEnd({
                result: 'fail'
            });
            this.node.active=false;
            return;
        }else{

        }
        this.hideQuestion();
    }
});
