import global from './../../global'
cc.Class({
    extends: cc.Component,

    properties: {
        bgList: {
            default: [],
            type: cc.Node
        },
        bgSpeed: 0,
        bgType: "middle",
        speedScaleRate: 0,
        speedOffset: 0,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        let self = this;
        if (self.bgType === "middle") {
            self.bgSpeed = global.account.playerData.speed+self.speedOffset;
        }
        if (self.bgType === "forward") {
            self.bgSpeed = global.account.playerData.speed + 80 + self.speedOffset;
        }
        if (self.bgType === "after") {
            self.bgSpeed = global.account.playerData.speed - 200 + self.speedOffset;
        }
        global.account.gameCtl.event.on("speed-changed", function (opt) {
            if (self.bgType === "middle") {
                console.log("on speed changed,", opt.speedValue);
                self.bgSpeed = opt.speedValue + self.speedOffset;
            }
            if (self.bgType === "forward") {
                console.log("on speed changed,", opt.speedValue);
                self.bgSpeed = opt.speedValue + 80 + self.speedOffset;
            }
            if (self.bgType === "after") {
                console.log("on speed changed,", opt.speedValue);
                self.bgSpeed = opt.speedValue - 200 + self.speedOffset;
            }
        });
    },
    update(dt){
        if (!global.account.gameCtl.isRunning()||global.account.gameCtl.state()===GameState.endScene) {
            return;
        }
        //
        let dis = this.bgSpeed * dt * defines.timeScale;
        for (let i in this.bgList) {
            this.bgList[i].x -= dis;
        }

        //
        if (this.bgList[0].x <= -1000) {
            let item = this.bgList.shift();
            let lastItem = this.bgList[this.bgList.length - 1];
            item.x = lastItem.x + lastItem.width;
            this.bgList.push(item);
        }
    }
});
