import global from './../../global'
cc.Class({
    extends: cc.Component,

    properties: {
        stubSpriteFrameList: {
            default: [],
            type: cc.SpriteFrame
        }
    },
    initWithData: function (data) {
        this.node.dead=false;
        this.speedX = data.speed;
        this.oldSpeedX = this.speedX;
        let isQuick = data.isQuick;
        console.log('is qucik' + isQuick);
        if (isQuick){
            this.node.emit('quick');
        }

        this.fly = false;
    },
    onLoad(){
        let self=this;
        if (this.stubSpriteFrameList.length !== 0){
            this.addComponent(cc.Sprite).spriteFrame = this.stubSpriteFrameList[0];
            let collider = this.node.addComponent(cc.BoxCollider);
            collider.size = cc.size(this.node.width, this.node.height);
        }
        this.node.on('fly', ()=>{
            this.fly = true;
            console.log('');
        });

        global.account.gameCtl.event.on("speed-changed",function(opt){
            // console.log("player speed changed:",opt.speedValue);
            // self.speedX =opt.speedValue;
        });
    },
    update(dt){
        if (global.account.gameCtl.isRunning()){
            if (this.speedX && this.fly === false){
                this.node.position = cc.p(this.node.position.x - this.speedX * dt * defines.timeScale, this.node.position.y);
            }
        }

        if ((this.node.position.x + this.node.width * 0.5) < cc.winSize.width * -0.5 ){
            this.node.dead = true;
        }
    }
});
