import global from './../../global'

const EnemyState = {
    invalid: -1,
    run: 1,
    tipsBegan: 2,
    tipsEnd: 3

};
cc.Class({
    extends: cc.Component,

    properties: {
        body:cc.Node,
    },

    onLoad() {
        this.state = EnemyState.invalid;
        this.setState(EnemyState.run);
        let animationName="idle_"+(parseInt(Math.random()*10)%3);
        this.body.getComponent(dragonBones.ArmatureDisplay).playAnimation(animationName,-1);
        this.node.on('fly', () => {
            console.log('');
            let action1 = cc.bezierTo(0.5, [cc.p(78, 9), cc.p(86, 302), cc.p(418, 600)]);
            let rotation = cc.rotateTo(0.5, 360 * 4);
            let scal1 = cc.scaleTo(0.5, 0.2, 0.2);
            let finished=cc.callFunc(() => {
                console.log("enemy dead");
                this.node.dead = true;
            });
            this.node.runAction(cc.sequence(cc.spawn([action1,rotation,scal1]),finished));
            this.node.getComponent(cc.BoxCollider).enabled=false;
            // this.node.runAction(raaction);
            // this.setState(EnemyState.tipsEnd);
            this.node.isFly = true;
        });

        // global.account.gameCtl.onPause(() => {
        //     this.node.emit('time-scale', 0);
        // });
        // global.account.gameCtl.onResume(() => {
        //     this.node.emit('time-scale', defines.timeScale);
        // });

    },
    onEnable(){
        this.node.getComponent(cc.BoxCollider).enabled=true;
        this.state=EnemyState.run;
        this.node.scale=1;
    },
    update() {
        if (this.state === EnemyState.run && !global.account.gameCtl.isQuick()) {
            let dis = cc.pDistance(this.node.position, global.account.playerData.playerNode.position);
            let player=global.account.playerData.playerNode.getComponent("player");
            let speedX=player.speeds[player.speedLevel];
            let animationScale=speedX/player.speeds[0];
            if (dis/(speedX/animationScale)<2.0) {
                player.node.emit("attack");
                // this.setState(EnemyState.tipsEnd);
                // this.setState(EnemyState.tipsBegan);
            }
            if (dis/speedX/animationScale<=2.0){
                this.setState(EnemyState.tipsBegan);
            }
        }
    },
    setState(state) {
        if (this.state === state) {
            return
        }
        switch (state) {
            case EnemyState.tipsBegan:
                global.account.gameCtl.tipsBegan();
                break;
            case EnemyState.tipsEnd:
                break;
            default:
                break;
        }
        this.state = state;
    },
    onDestroy() {
        console.log('');
    }
});
