import global from "../global";

cc.Class({
    extends: cc.Component,

    properties: {
        terrainPrefabs:[cc.Prefab],

        ratesShowTerrain:[cc.Float],
        activeEnemies:[cc.Prefab],
        wordEnemies:[cc.Prefab],
        superModeTerrain:cc.Prefab,

        distanceAddTerrain:10,
        durationAddEnemy:20,
        durationAddWordEnemy:60,
        durationAddSuperTerrain:3,
        intervalAddWordEnemy:3,

        initPosNodeTerrain:cc.Node,
        initPosNodeActiveEnemy:cc.Node,
    },
    onLoad() {
        this.passedTimeForTerrain=0;
        this.passedTimeForEnemy=0;
        this.passedTimeForWordEnemy=0;
        this.passedTimeForSuper=0;
        this.totalTerrains=0;

        this.sceneIndex=global.account.gameCtl.getSceneIndex();

        this.terrains=[];
        let prefabs=this.terrainPrefabs[this.sceneIndex];
        for (let i=0;i<prefabs.length;i++){
            this.terrains.push(cc.instantiate(prefabs[i]));
        }
    },

    randTerrain(terrainList,rateList){
        if(terrainList.length!==rateList.length){
            console.error("terrainList and rateList must have same length");
            return;
        }
        let v=Math.random();
        let ret=null;
        for (let i=0;i<rateList.length;i++){
            if (i>=rateList.length-1){
                ret=cc.instantiate(terrainList[i]);
                break;
            }
            if (v<rateList[i]){
                ret=cc.instantiate(terrainList[i]);
                break;
            }
        }
        // terrainList.splice(terrainList.indexOf(ret),1);
        return ret;
    },
    update(dt) {
        if(!global.account.gameCtl.isRunning()||global.account.gameCtl.state()===GameState.tipsBegan){
            return;
        }
        //
        if (global.account.playerData.distance%global.account.playerData.sceneDistance>=global.account.playerData.sceneDistance-20||global.account.gameCtl.state()===GameState.endScene){
            return;
        }

        //
        // this.passedTimeForWordEnemy+=dt;
        // if(this.passedTimeForWordEnemy>=this.durationAddWordEnemy&&global.account.gameCtl.state()!==GameState.quick){
        //     this.addWordEnemy();
        //     this.passedTimeForWordEnemy=0;
        //     this.passedTimeForTerrain=0;    //
        // }

        if (global.account.gameCtl.state()===GameState.quick){
            this.passedTimeForSuper-=dt;
            if(this.passedTimeForSuper<=0){
                console.log("add super terrain");
                this.passedTimeForSuper=this.durationAddSuperTerrain;
                let terrain=cc.instantiate(this.superModeTerrain);
                terrain.position=this.initPosNodeTerrain.position;
                terrain.parent=this.initPosNodeTerrain.parent;
            }
        }

        //
        this.passedTimeForTerrain+=dt;
        if (parseInt(global.account.playerData.distance%global.account.playerData.sceneDistance)%this.distanceAddTerrain===0&&this.passedTimeForTerrain>=1){
            // console.log("enter add terrain:",this.passedTimeForTerrain,this.node._id);
            //
            this.passedTimeForTerrain=0;
            if(this.totalTerrains%this.intervalAddWordEnemy===0&&this.totalTerrains>0){
                this.addWordEnemy();
            }else{
                this.addTerrain();
            }
            this.totalTerrains++;
        }

        //
        this.passedTimeForEnemy+=dt;
        if (this.passedTimeForEnemy>=this.durationAddEnemy){
            this.addActiveEnemy();
            this.passedTimeForEnemy=0;
        }
    },
    addActiveEnemy(){
        //100.5
        let v=Math.random();
        if(v>0.5){
            return;
        }
        let enemy=cc.instantiate(this.activeEnemies[this.sceneIndex]);
        enemy.position=this.initPosNodeActiveEnemy.position;
        enemy.parent=this.initPosNodeActiveEnemy.parent;
    },
    addTerrain(){
        let terrain=this.randTerrain(this.terrainPrefabs,this.ratesShowTerrain);
        if(this.lastTerrain&&this.lastTerrain.width+this.lastTerrain.x>this.initPosNodeTerrain.position.x-400){
            return;
        }
        terrain.position=this.initPosNodeTerrain.position;
        for (let i in terrain.children){
            terrain.children[i].enable=true;
        }
        terrain.parent=this.initPosNodeTerrain.parent;
        this.lastTerrain=terrain;
    },
    addWordEnemy(){
        let enemy=cc.instantiate(this.wordEnemies[this.sceneIndex]);
        enemy.position=this.initPosNodeTerrain.position;
        enemy.parent=this.initPosNodeTerrain.parent;
    }
});
