import global from '../global'

cc.Class({
    extends: cc.Component,

    properties: {
        totalTime: 0,
        progressTime: {
            default: null,
            type: cc.ProgressBar
        },
        processTimeSprite:cc.Node,
        btnJump: {
            default: null,
            type: cc.Node
        },
        progressSpeed: {
            default: null,
            type: cc.ProgressBar
        },
        labelCoinCount: {
            default: null,
            type: cc.Label
        },
        labelDistance:cc.Label,
        progressSuperPower:cc.ProgressBar,
        pauseLayer: {
            default: null,
            type: cc.Prefab
        },
        particle:cc.Node,
    },

    start(){

    },
    onLoad() {
        let self=this;
        //ui
        this.labelCoinCount.string=global.account.playerData.goldCount;
        this.progressSpeed.progress=global.account.playerData.speedLevel/4;
        this.progressTime.progress=(this.totalTime-global.account.playerData.passedTime)/this.totalTime;

        this.progressSuperPower.progress=global.account.playerData.power/4;
        global.account.gameCtl.event.on("speed-changed",function(opt){
            self.progressSpeed.progress=opt.currentLevel/opt.maxLevel;
        });

        global.account.gameCtl.event.on("quick",function(opt){
            console.log("quick mode begin");
            self.btnJump.getComponent(cc.Button).enabled=false;
        });
        global.account.gameCtl.event.on("quick-end",function(opt){
            console.log("game resume");
            self.btnJump.getComponent(cc.Button).enabled=true;
            self.progressSuperPower.progress=global.account.playerData.power/4;
        });

        global.account.gameCtl.onTipsBegan((data) => {
            this.btnJump.opacity = 100;
            // this.runButton.opacity = 100;
        });
        global.account.gameCtl.onTipsEnd((data)=>{
            this.btnJump.opacity = 255;
            // this.runButton.opacity = 255;
            this.node.getComponent(cc.Animation).play("speed_blink");
            this.progressSuperPower.progress=global.account.playerData.power/4;
        });
        global.account.gameCtl.onAttackEnd(() => {
            //
            this.btnJump.opacity = 255;
            this.runButton.opacity = 255;
        });
        // global.account.gameCtl.onDistanceChange((distance)=>{
        //     this.distanceNode.emit('distance-change', distance);
        // });

        cc.eventManager.addListener({
            event: cc.EventListener.KEYBOARD,
            onKeyPressed: (keyCode, event) => {
                this._keyHandler(keyCode, true);
            },
            onKeyReleased: (keyCode, event) => {
                this._keyHandler(keyCode, false);
            },
        }, this.node);

        this.isCanRun = false;
        // this.runButtonIcon.active = false;
        this.passedPixel=0;

    },
    _keyHandler(code, isDown){
        switch (code){
            case cc.KEY.up:
            case cc.KEY.space:
                console.log('');
                if (this.btnJump.opacity === 255){
                    global.account.gameCtl.event.fire('jump');
                }
                break;
            case cc.KEY.enter:
                console.log('');
                // this.player.emit('quick');
                if (this.isCanRun){
                    global.account.gameCtl.quick();
                }
                break;
            default:
                break;
        }
    },
    onPauseClicked(){
        let node = cc.instantiate(this.pauseLayer);
        node.parent = this.node;
        global.account.gameCtl.pause();
        cc.audioEngine.pauseAll();
    },
    onButtonClick: function (event, customData) {
        switch (customData) {
            case 'up-button':
                //
                if (this.btnJump.opacity === 255) {
                    global.account.gameCtl.event.fire('jump');
                }
                break;
            case 'run-button':
                // if (this.runButton.opacity === 255 && this.isCanRun) {
                //     console.log('run button');
                //     global.account.gameCtl.quick();
                //     // this.runButtonIcon.active = false;
                // }
                break;
            default:
                break;
        }
    },
    update(dt) {
        if (global.account.gameCtl.isRunning()){
            global.account.playerData.passedTime += dt;
            //
            this.progressTime.progress=(this.totalTime - global.account.playerData.passedTime) / this.totalTime;
            this.particle.x=this.progressTime.node.x+this.processTimeSprite.width;    //
            //
            this.labelCoinCount.string = global.account.playerData.goldCount + '';
            //
            this.labelDistance.string=parseInt(global.account.playerData.distance);


            // if (global.account.gameCtl.isQuick()){
            //     if (this.progressSpeed.progress > 0){
            //         this.progressSpeed.progress -= this.progressSpeedSpeed;
            //         if (this.progressSpeed.progress <= 0){
            //             this.progressSpeed.progress = 0;
            //             this.isCanRun = false;
            //             global.account.gameCtl.qucikEnd();

            //         }
            //     }
            // }else {
            //     if (this.progressSpeed.progress < 1){
            //         this.progressSpeed.progress += this.progressSpeedSpeed;
            //         if (this.progressSpeed.progress >= 1){
            //             this.progressSpeed.progress = 1;
            //             console.log('');
            //             this.isCanRun = true;
            //             this.runButtonIcon.active = true;

            //         }
            //     }
            // }

        }
    }
});
