import global from '../../global'
cc.Class({
    extends: cc.Component,

    properties: {
        accY: 0,
        jumpSpeed: 0,
    },
    onLoad(){
        let self=this;
        let oldAccY = this.accY;
        let oldJumpSpeed = this.jumpSpeed;
        global.account.gameCtl.event.on("speed-changed",function(opt){
            if(opt.currentLevel===0){
                self.jumpSpeed= oldJumpSpeed;
            }else{
                self.jumpSpeed= self.jumpSpeed+50;
            }
        });
        global.account.gameCtl.event.on('jump', this.jump.bind(this));
        this.initPosY = this.node.position.y;
        this.timeSubSpeed = 0;
    },
    jump(){
        console.log("on jump");
        if(this.speedY===undefined){
            this.speedY =this.jumpSpeed;
        }
    },
    playJumpSound(sound){
        if (!this.playedSound){
            this.playedSound=true;
            cc.audioEngine.play(sound,false,1);
        }
    },
    update: function (dt) {
        if (this.speedY !== undefined){
            this.node.position = cc.p(this.node.position.x, this.node.position.y + this.speedY * dt * defines.timeScale);
            this.speedY += (this.accY * dt * defines.timeScale);
        }
        if (this.node.position.y <= this.initPosY && this.speedY !== undefined){
            this.node.position = cc.p(this.node.position.x, this.initPosY);
            this.speedY = undefined;
            console.log('');
            this.node.emit('jump-end');
            this.playedSound=false;
        }

        // if (defines.timeScale > 0.1 && this.timeSubSpeed !== 0){
        //     defines.timeScale += this.timeSubSpeed;
        //     setTimeout(()=> {
        //         defines.timeScale = 1;
        //         this.timeSubSpeed = 0;
        //     }, 1000);
        // }

    }
});
