import global from './../global'
cc.Class({
    extends: cc.Component,

    properties: {
        body: {
            default: null,
            type: cc.Node
        },
        car:cc.Node,
        jumpAudio: cc.AudioClip,
        attackAudio: cc.AudioClip,
        eatAudio: cc.AudioClip,
        beAttacked: cc.AudioClip,
        // backgroundMusic: cc.AudioClip,
        auCar:cc.AudioClip,
        speeds: [cc.Float],
        speedLevel: 0,
        distanceCurrentScene:0,
        superPowerDuration:10,
        superSpeed:700,
        rootNode:cc.Node,
    },

    onLoad(){
        this.passedTimeSuper=0;
        this.speedLevel=global.account.playerData.speedLevel;
        this.speed=global.account.playerData.speed;
        // cc.audioEngine.play(this.backgroundMusic, true, 1);
        global.account.gameCtl.onTipsBegan(()=> {
            //
            console.log('');
            // this.node.emit('idle');
            // this.node.emit('time-scale');

        });
        global.account.gameCtl.onTipsEnd((data)=> {
            console.log('on tip end');
            if(global.account.playerData.power>=4){
                this.enterSuperMode();
            }
            this.tipsResult = data.result;
            // this.node.emit('attack');
            this.node.emit('time-scale', 1);
        });
        this.node.zIndex = 1;
        global.account.gameCtl.event.on('jump', this.onJump.bind(this));

        global.account.gameCtl.onTimeScale(()=> {
            this.node.emit('time-scale', defines.timeScale);
        });

        global.account.gameCtl.onPause(()=> {
            this.node.emit('time-scale', 0);
        });
        global.account.gameCtl.onResume(()=> {
            this.node.emit('time-scale', defines.timeScale);
        });
        global.account.playerData.playerNode = this.node;
    },

    getNowSpeed() {
        return this.speeds[this.speedLevel];
    },

    onJump(event){
        console.log("on jump audio");
        let com = this.node.getComponent("jump");
        com.playJumpSound(this.jumpAudio);
    },

    onCollisionEnter: function (other, self) {
        if (other.getComponent('enemy')) {
            cc.audioEngine.play(this.attackAudio, false, 1);
            if(global.account.gameCtl.state()!==GameState.quick){
                other.node.emit('fly');
                this.node.emit('walk');
                // setTimeout(()=> {
                //     this.node.emit('walk');
                // }, 1);

                this.speedLevel++;
                this.speedLevel = Math.min(this.speedLevel, this.speeds.length - 1);
                this.changeSpeed(this.speeds[this.speedLevel]);
            }else{
                other.node.emit('fly');
            }
        }

        if (other.getComponent("stub-tag")&&global.account.gameCtl.state()!==GameState.quick) {
            this.speedLevel--;
            this.speedLevel = Math.max(this.speedLevel, 0);
            this.changeSpeed(this.speeds[this.speedLevel]);
            cc.audioEngine.play(this.beAttacked, false, 1);
            let animate = this.body.getComponent(cc.Animation);
            animate.play("be-attacked");
        }
        if (other.getComponent('coin')) {
            cc.audioEngine.play(this.eatAudio, false, 1);
            other.node.emit('fly');
            global.account.playerData.goldCount++;
        }
    },
    changeSpeed(newSpeed){
        global.account.playerData.speed=newSpeed;
        this.node.emit("time-scale", newSpeed / this.speeds[0]);
        global.account.gameCtl.event.fire("speed-changed", {
            currentLevel: this.speedLevel,
            maxLevel: this.speeds.length - 1,
            speedValue: newSpeed
        });
    },

    enterSuperMode(){
        this.body.active=false;
        this.car.active=true;
        this.passedTimeSuper=0;

        this.carAudiosID=cc.audioEngine.play(this.auCar,true);

        this.changeSpeed(this.superSpeed);
    },

    exitSuperMode(){
        this.passedTimeSuper=0;
        this.body.active=true;
        this.car.active=false;

        if(this.carAudiosID){
            cc.audioEngine.stop(this.carAudiosID);
        }

        global.account.gameCtl.qucikEnd();
        this.changeSpeed(this.speeds[this.speedLevel]);
    },

    update(dt){
        if(!global.account.gameCtl.isRunning()||global.account.gameCtl.state()===GameState.tipsBegan){
            return;
        }

        let speed=0;
        if (global.account.gameCtl.state()!==GameState.quick){
            speed=this.speeds[this.speedLevel];
        }else{
            speed=this.superSpeed;
            this.passedTimeSuper+=dt;
            if(this.passedTimeSuper>=this.superPowerDuration){
                this.exitSuperMode();
            }
        }

        if (global.account.gameCtl.state()===GameState.endScene){
            this.node.x+=speed*dt;
            if (this.node.x>600){
                this.node.active=false;
                this.rootNode.active=false;
                this.exitSuperMode();
                global.account.gameCtl.nextScene();
            }
            return;
        }

        let distance=(speed*dt/150);
        global.account.playerData.distance+=distance;
        this.distanceCurrentScene+=distance;
        if(this.distanceCurrentScene>global.account.playerData.sceneDistance&&global.account.gameCtl.state()!==GameState.endScene){
            this.rootNode.getComponent(cc.Animation).play("player_out");
            global.account.gameCtl.setState(GameState.endScene);
        }
    },
    onDisable(){
        if(this.carAudiosID){
            cc.audioEngine.stop(this.carAudiosID);
        }
    },
});
