import global from './../../global';
cc.Class({
    extends: cc.Component,

    properties: {

    },
    onLoad(){
        this.initPos=this.node.position;
        this.node.dead = false;
        this.p=this.node.convertToNodeSpace(cc.p(-212,200));
        this.actionFlyToPlayer=cc.moveTo(1.5,this.p);
        // this.playerDir=cc.pNormalize(this.pp,this.node.position);
        this.node.on('fly', ()=>{
            if(this.flying){
                this.node.removeFromParent(true);
                return;
            }
            this.dead=true;
            let action = cc.bezierTo(0.3, [this.node.position,cc.p(this.node.x, this.node.y + 200), cc.p(-530,350)]);
            this.node.runAction(cc.sequence(action, cc.callFunc(()=>{
                console.log('');
                // this.node.dead = true;
                // this.node.x = -1000;
                this.node.removeFromParent(true);
            })));
            this.node.runAction(cc.scaleTo(0.4, 0.2,0.2));
        });
        this.node.on('pause',()=>{
           //
           this.node.getComponent(cc.Animation).pause();
        });
        this.node.on('resume',()=>{
           //
            this.node.getComponent(cc.Animation).resume();
        });
    },
    onEnable(){
        console.log("on enable");
        this.node.position=this.initPos;
        this.node.scale=1;
    },
    onDestroy(){
        console.log('');
    },
    update(dt){
        if(this.dead||this.flying){
            return;
        }
        //
        if(global.account.gameCtl.state()===GameState.quick){
            this.flying=true;
            this.node.runAction(this.actionFlyToPlayer);
        }
    }
});
