cc.Class({
    extends: cc.Component,

    properties: {
        numSpriteFrameList: {
            default: [],
            type: cc.SpriteFrame
        },
        mileIcon: {
            default: null,
            type: cc.Node
        }
    },
    onLoad(){
        this.numNodeMap = {};
        this.setNumber(0);
        this.node.on('distance-change',  (event)=> {
            let detail = event.detail;
            this.setNumber(detail);
        })
    },
    setNumber(value){
        // let node = cc.Node('node');
        let str = value + '';
        for (let i = str.length  - 1 ; i >= 0 ; i --){
            let char = str[i];
            if (this.numNodeMap.hasOwnProperty(i)){
               this.numNodeMap[i].getComponent(cc.Sprite).spriteFrame = this.numSpriteFrameList[char];
            }else {
                this.numNodeMap[i] = new cc.Node('node');
                this.numNodeMap[i].parent = this.node;
                let sp = this.numNodeMap[i].addComponent(cc.Sprite);
                sp.spriteFrame = this.numSpriteFrameList[char];
                this.numNodeMap[i].x +=this.numNodeMap[i].width * i;
                this.mileIcon.x = this.numNodeMap[i].x + this.numNodeMap[i].width * 0.5 + this.mileIcon.width * 0.5;

            }
        }

    }
});
