
import global from './../global';
cc.Class({
    extends: cc.Component,

    properties: {
        playerNode: {
            default: null,
            type: cc.Node
        },
        uiNode:cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {

    },

    start () {
        let self=this;
        console.log("on game loaded");
        //ipadui
        let size=cc.view.getFrameSize();
        console.log(size.height,size.width);
        if(size.height/size.width>0.6){
            console.log("ipad,scale ui");
            this.uiNode.scale=0.75;
        }
        cc.director.getCollisionManager().enabled = true;

        this.animation=this.node.getComponent(cc.Animation);
        this.animation.on("finished",this.onAnimationFinished.bind(this));
        //
        let resList = [];
        for (let i in defines.config){
            resList.push(defines.config[i]);
        }

        global.resourcesManager.loadList(resList, ()=>{
            this.animation.play("player_in");
            global.account.gameCtl.run();
            self.playerNode.emit("walk");
        });
    },
    onAnimationFinished(event){
        // var state = event.detail;    // state instanceof cc.AnimationState
        // var type = event.type;       // type === 'play';
        // if(state.name==="player_in"){
        //     let self=this;
        //     self.playerNode.emit('idle');
        //     setTimeout(function () {
        //         global.account.gameCtl.run();
        //         self.playerNode.emit("walk");
        //     },1500)
        // }
    },
    update(dt){
        global.account.gameCtl.update(dt);
    }
});
