import global from './../../global'
cc.Class({
    extends: cc.Component,

    properties: {

    },
    onLoad() {

    },
    onButtonClick(event, customData){
        switch (customData){
            case 'resume':
                global.account.gameCtl.resume();
                this.node.destroy();
                cc.audioEngine.resumeAll();
                break;
            default:
                break;
        }
    }
});
